import { ChangeDetectorRef, Component, OnInit, ViewChild } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { TranslateConfigService } from 'src/app/services/translate-config.service';
import { LoggerServiceProvider } from 'src/providers/logger-service/logger-service';
import { ConfigProvider } from 'src/providers/utils/config';
import { UtilService } from 'src/providers/utils/utils.service';
import { PaymentHistoryService } from './paymwntHistoryService';
import { MenuOptions } from 'src/app/app.component';
import * as HttpStatus from 'http-status-codes';


@Component({
  selector: 'app-payment-history',
  templateUrl: './payment-history.page.html',
  styleUrls: ['./payment-history.page.scss'],
})
export class PaymentHistoryPage implements OnInit {
  @ViewChild('Content') content: any;

  selectedLanguage:string;
  paymentHistoryList: any = [];
  isShowingLoader = true;
  errorMsg: any;
  canTryAgain: any = false;
  isEMandate:boolean = false;

  selctedIndex: any;
  constructor(
    public utils: UtilService,
    public logger: LoggerServiceProvider, public getPaymentHistoryList: PaymentHistoryService,
    public translate: TranslateService, public changeDetectorRef: ChangeDetectorRef,
    private config: ConfigProvider, private translateConfigService: TranslateConfigService,
  ) {
    this.selectedLanguage = this.translateConfigService.getDefaultLanguage();
    this.translateConfigService.setLanguage('en');
   }

   ngOnInit() {
    this.getPaymentHistory();
  }

  ionViewDidLoad() {
    this.logger.log('ionViewDidLoad PaymentHistoryPage');

  }



  /**
   * In this method we are updating the side menu title
   */
  ionViewWillEnter() {
    if(!this.isEMandate){
      this.utils.component.emit(MenuOptions.PAYMENT_HISTORY);
    } else {
      this.utils.component.emit(MenuOptions.DD_HISTORY);
    }

  }

  /**
   * this method is used to list of Payment Transactions
   */
  getPaymentHistory() {
    if (this.utils.isDeviceOnLine()) {
      this.canTryAgain = false;
      this.getPaymentHistoryList.getPaymentHistory(this.isEMandate).then((res) => {
        try {
          this.isShowingLoader = false;
          if (res['responseJSON'] && (res['responseJSON']['statusCode'] == HttpStatus.OK)) {
            this.paymentHistoryList = res['responseJSON']['array'];
            if (this.paymentHistoryList.length <= 0) {
              this.errorMsg = this.translate.instant('generic_msgs.no_data');
            } else {
              if(this.isEMandate){
                this.paymentHistoryList.sort((a: any, b: any) =>
                new Date(b.TransactionDate.split('-').reverse().join('-')).getTime() - new Date(a.TransactionDate.split('-').reverse().join('-')).getTime());
              } else {
                this.paymentHistoryList.sort((a: any, b: any) =>
                new Date(b.PaymentDate.split('-').reverse().join('-')).getTime() - new Date(a.PaymentDate.split('-').reverse().join('-')).getTime());
              }
                 }
          } else {
              this.errorMsg = res['responseJSON']['Message'] || res['responseJSON']['userMessage'] || res['responseJSON']['message']|| this.translate.instant('generic_msgs.server_err');
            }
        } catch (e) {
          this.isShowingLoader = false;
          this.canTryAgain = true;
          this.logger.error("getPaymentHistory catch eror : " + JSON.stringify(e));
          this.errorMsg = this.translate.instant('generic_msgs.server_err');
        }
      }, (err) => {
        this.isShowingLoader = false;
        this.canTryAgain = true;
        this.errorMsg = this.utils.handleServiceException(err);
      });
    } else {
      this.isShowingLoader = false;
      this.canTryAgain = true;
      this.errorMsg = this.translate.instant('generic_msgs.network_err');

    }
  }



  /**toggle */
  expandItem(item, e) {
    this.paymentHistoryList.map((listItem) => {
      if (item == listItem) {
        listItem.expanded = !listItem.expanded;
        if (listItem.expanded) {
          setTimeout(() => {
            let itemIndex = this.paymentHistoryList.indexOf(item);
            let element = this.content._elementRef.nativeElement.childNodes[1].childNodes[6].childNodes[itemIndex + 2].offsetTop;
            this.content.scrollTo(0, element, 1000);
          }, 200);
        }
      } else {
        listItem.expanded = false;
      }
      return listItem;
    });
  }
}
